import React from 'react';
import { useChainSpecific } from 'hooks/chainSpecific'
import { Grid } from 'components/Box'
import EventCard from './components/EventCard';
import Section from './components/Section'


const Home: React.FC = () => {
  const { saverAddr, tokens } = useChainSpecific()
  if (!saverAddr || tokens.length === 0) return null
  const Events = tokens.map(token => (
    <EventCard key={token.address} saverAddr={saverAddr} tokenAddr={token.address} tokenSymbol={token.symbol} />
  ))
  return (
    <Section title=''>
      <Grid
        gridTemplateColumns={['1fr', '1fr 1fr', '1fr 1fr 1fr' ]}
        gridGap='20px'
      >
        {Events}
      </Grid>
    </Section>
  )
}

export default Home;
