import { useMemo } from 'react'
import { saverAddressConfig } from 'metaData/saver'
import { tokenAddressConfig } from 'metaData/token'
import { chainParams } from 'metaData/chain'
import { useWeb3 } from 'hooks/web3'
import { testIsSupportedChain } from './helper'

export const useChainSpecific = () => {
  const { chainId } = useWeb3()
  const saverAddr = useMemo(() => {
    return chainId && testIsSupportedChain(chainId) ? saverAddressConfig[chainId] : undefined
  }, [chainId])

  const tokens = useMemo(() => {
    return chainId && testIsSupportedChain(chainId) ? tokenAddressConfig[chainId] : []
  }, [chainId])
  return {
    saverAddr,
    tokens
  }
}

export const useChainInfo = () => {
  const { chainId } = useWeb3()

  const chainInfo = useMemo(() => {
    return chainId && testIsSupportedChain(chainId)
      ? chainParams[chainId]
      : undefined
  }, [chainId])

  return {
    isSupportedChain: chainInfo !== undefined,
    chainInfo,
  }
}
