import React, { useMemo } from 'react'
import styled from 'styled-components'
import { useWeb3 } from 'hooks/web3'
import ConnectWallet from './ConnectWallet'
import { useChainInfo } from 'hooks/chainSpecific'
import { Text } from 'components/Text'
import ThemeSwitcher from './ThemeSwitcher'
import { useTheme } from 'hooks/theme'
import { Flex } from './Box'

const LabelText = styled.span`
  color: ${({ theme }) => theme.colors.text};
  font-weight: 600;
  padding-right: 12px;
`;

interface NetworkTipProps {
  isCorrectNetwork: boolean
}
const NetworkTip = styled.span<NetworkTipProps>`
  color: ${({ theme, isCorrectNetwork }) => isCorrectNetwork ? theme.colors.textSubtle : theme.colors.failure };
  font-weight: 800;
  padding-right: 8px;
`;


const Header: React.FC = () => {
  const { account } = useWeb3()
  const { isSupportedChain, chainInfo } = useChainInfo()
  const networkName = isSupportedChain ? chainInfo?.chainName : ''

  const shortAccount = useMemo(() => {
    const getShortAddr = (addr: string, prefixLen: number, sufixLen: number, replaceWith: string) => {
      if (addr.length <= prefixLen + sufixLen) {
        return addr;
      }
      const prefix = addr.slice(0, prefixLen);
      const suffix = addr.slice(addr.length - sufixLen);
      return `${prefix}${replaceWith}${suffix}`;
    }
    return getShortAddr(account || '', 2, 4, '...')
  }, [account])

  const { isDark, setTheme } = useTheme()

  const toggleTheme = useMemo(() => {
    return () => setTheme(isDark ? 'light' : 'dark')
  }, [setTheme, isDark])


  return (
    <Flex justifyContent='space-between' alignItems='center' px={[2, 3, 4]} py={3} borderBottom={1}>
      <div>
        <Text bold fontSize={[3, 4, 5]}>
          Meta Ventures Dao
        </Text>
      </div>
      <div>
        {account
          ?
          <>
            <NetworkTip isCorrectNetwork={isSupportedChain}>
              { isSupportedChain ? networkName : '' }
            </NetworkTip>
            <LabelText>
              {shortAccount}
            </LabelText>
          </>
          :
          <ConnectWallet />
        }
        <ThemeSwitcher isDark={isDark} toggleTheme={toggleTheme} />
      </div>
    </Flex>
  )
}

export default Header
