import React, { useMemo } from 'react'
import { getReadableTimestamp } from 'utils/time'

interface Props {
  timestamp: number
}
const ReadableTimestamp: React.FC<Props> = ({ timestamp }) => {
  const readable = useMemo(() => {
    return getReadableTimestamp(timestamp)
  }, [timestamp])
  return (
    <span>{readable}</span>
  )
}

export default ReadableTimestamp
