import { BigNumber, ethers } from 'ethers';

export const fromBigToDisplayNum = (big: BigNumber, decimals: number = 18) => {
  return Number(ethers.utils.formatUnits(big, decimals))
}

export const fromDisplayNumToBig = (displayNum: number | string, decimals: number = 18) => {
  return ethers.utils.parseUnits(displayNum.toString(), decimals)
}

export const fromBigToNum = (big: BigNumber) => {
  return big.toNumber()
}

export const fromNumToBig = (num: number) => {
  return BigNumber.from(num)
}

export const MAX_APPROVE_AMOUNT = Number.MAX_SAFE_INTEGER
