import { useCallback, useEffect, useState } from 'react'
import { useSaverContract, useTokenContract } from 'hooks/contract'
import { fromBigToDisplayNum, fromDisplayNumToBig } from 'utils/number'
import { waitForConfirm } from 'utils/contractHelper'
import { useWeb3 } from 'hooks/web3'

export interface Pool {
  lockTill: number
  totalLocked: number
  userLocked: number
  userAllowance: number
}

const useAllowance = (tokenAddress: string, saverAddress: string) => {
  const { account } = useWeb3()
  const tokenContract = useTokenContract(tokenAddress)
  const [allowance, setAllowance] = useState<number>(0)

  const updateAllowance = useCallback(async () => {
    if (!tokenContract || !account) return
    try {
      const _allowance = await tokenContract.allowance(account, saverAddress)
      setAllowance(fromBigToDisplayNum(_allowance))
    } catch(e) {
      console.error(e)
    }
  }, [setAllowance, account, saverAddress, tokenContract])

  useEffect(() => {
    updateAllowance()
  }, [updateAllowance])

  return { allowance, updateAllowance }
}

export type LockProgram = {
  startTime: number
  endTime: number
  rewardRatio: number
}
const useLockProgram = (tokenAddress: string, saverAddress: string) => {
  const [lockProgram, setLockProgram] = useState<LockProgram>({ startTime: 0, endTime: 0, rewardRatio: 0 })
  const saverContract = useSaverContract(saverAddress)

  const updateLockProgram = useCallback(async() => {
    if (!saverContract) return
    try {
      const [startTime, endTime, rewardRatio] = await saverContract.tokenLockPrograms(tokenAddress)
      const program = {
        startTime: startTime.mul(1000).toNumber(),
        endTime: endTime.mul(1000).toNumber(),
        rewardRatio: rewardRatio.toNumber() / 10**8,
      }
      setLockProgram(program)
    } catch(e) {
      console.error(e)
    }
  }, [setLockProgram, saverContract, tokenAddress])

  useEffect(() => {
    updateLockProgram()
  }, [updateLockProgram])
  return { lockProgram, updateLockProgram }
}

const useTotalLocked = (tokenAdress: string, saverAddress: string) => {
  const tokenContract = useTokenContract(tokenAdress)
  const [totalLocked, setTotalLocked] = useState<number>(0)

  const updateTotalLocked = useCallback(async () => {
    if (!tokenContract) return
    const balance = await tokenContract.balanceOf(saverAddress)
    setTotalLocked(fromBigToDisplayNum(balance))
  }, [setTotalLocked, tokenContract, saverAddress])

  useEffect(() => {
    updateTotalLocked()
  }, [updateTotalLocked])

  return { totalLocked, updateTotalLocked }
}

const useUserLocked = (tokenAdress: string, saverAddress: string) => {
  const saverContract = useSaverContract(saverAddress)
  const [userLocked, setUserLocked] = useState<number>(0)

  const updateUserLocked = useCallback(async () => {
    if (!saverContract) return
    const balance = await saverContract.getUserTokenBalance(tokenAdress)
    setUserLocked(fromBigToDisplayNum(balance))
  }, [setUserLocked, saverContract, tokenAdress])

  useEffect(() => {
    updateUserLocked()
  }, [updateUserLocked])

  return { userLocked, updateUserLocked }
}

const useBalanceRelated = (tokenAddress: string, saverAddress: string) => {
  const { totalLocked, updateTotalLocked } = useTotalLocked(tokenAddress, saverAddress)
  const { userLocked, updateUserLocked } = useUserLocked(tokenAddress, saverAddress)

  const saverContract = useSaverContract(saverAddress)
  const withdraw = useCallback(async (amount: string | number) => {
    if(!saverContract) return
    const amountBig = fromDisplayNumToBig(amount)
    await waitForConfirm(
      saverContract.withdrawToken(tokenAddress, amountBig)
    )
    updateTotalLocked()
    updateUserLocked()
  }, [saverContract, tokenAddress, updateUserLocked, updateTotalLocked])

  const deposit = useCallback(async (amount: string | number) => {
    if(!saverContract) return
    const amountBig = fromDisplayNumToBig(amount)
    await waitForConfirm(
      saverContract.depositToken(tokenAddress, amountBig)
    )
    updateTotalLocked()
    updateUserLocked()
  }, [saverContract, tokenAddress, updateUserLocked, updateTotalLocked])

  return { totalLocked, userLocked, withdraw, deposit }
}

const useApproveRelated = (tokenAddress: string, saverAddress: string) => {
  const { allowance, updateAllowance } = useAllowance(tokenAddress, saverAddress)
  const tokenContract = useTokenContract(tokenAddress)

  const approve = useCallback(async (amount: number) => {
    if (!tokenContract) return
    const _amount = fromDisplayNumToBig(amount)
    await waitForConfirm(
      tokenContract.approve(saverAddress, _amount)
    )
    updateAllowance()
  }, [updateAllowance, tokenContract, saverAddress])

  return {
    allowance, approve
  }
}


export const usePool = (tokenAddress: string, saverAddress: string) => {
  const { lockProgram } = useLockProgram(tokenAddress, saverAddress)
  const { totalLocked, userLocked, withdraw, deposit } = useBalanceRelated(tokenAddress, saverAddress)
  const { allowance, approve } = useApproveRelated(tokenAddress, saverAddress)
  return {
    lockProgram,
    totalLocked,
    userLocked,
    allowance,
    withdraw,
    deposit,
    approve,
  }
}
