import { useMemo } from 'react'
import { Contract, providers } from 'ethers'
import { useWeb3 } from 'hooks/web3'
import TOKEN_CONTRACT from 'abi/TokenContract.json'
import SAVER_CONTRACT from 'abi/SaverContract.json'
import { TokenContract, SaverContract } from 'abi/types'

const simpleRPCProvider = new providers.StaticJsonRpcProvider()

export const useContract = <T extends Contract = Contract>(
  address: string | undefined,
  ABI: any,
  withSignerIfPossible = true,
): T | null => {
  const { provider } = useWeb3()

  return useMemo(() => {
    if (!address || !ABI || !provider) return null
    try {
      return new Contract(address, ABI, withSignerIfPossible ? provider.getSigner() : simpleRPCProvider)
    } catch (error) {
      console.error('Failed to get contract', error)
      return null
    }
  }, [address, ABI, provider, withSignerIfPossible ]) as T
}

export const useTokenContract = (tokenAddr: string) => {
  return useContract<TokenContract>(tokenAddr, TOKEN_CONTRACT.abi)
}

export const useSaverContract = (saverAddr: string) => {
  return useContract<SaverContract>(saverAddr, SAVER_CONTRACT.abi)
}
