import invariant from 'invariant'
import { isInteger } from 'lodash'

const second = 1000;
const min = second * 60;
const hour = min * 60;
const day = hour * 24;
const year = day * 365;

const calDaysHoursMins = (timestamp: number) => {
  const units = [year, day, hour, min, second]
  const res = units.map(() => 0);

  const gap = timestamp - Date.now();
  if (gap <= 0) return res;

  let num = gap;
  for(let i = 0; i < units.length; i++) {
    const div = Math.floor(num / units[i]);
    const mod = num % units[i];
    num = mod;
    res[i] = div;
  }
  return res;
}

export const getReadableFromNow = (timestamp: number) => {
  const units = calDaysHoursMins(timestamp)
  const unitNames = ['', '', '', '', '']
  let res = ''
  for(let i = 0; i < units.length; i++) {
    if (units[i] > 0) {
      res += `${units[i]}${unitNames[i]}`
    }
  }
  return res || ''
}

export const getReadableTimestamp = (timestamp: number) => {
  invariant(isInteger(timestamp) && timestamp >= 0, 'illegal timestamp')
  const date = new Date(timestamp);
  const [month, day, year] = [date.getMonth(), date.getDate(), date.getFullYear()];
  return `${year}${month}${day}`
}

export const isFutureTimestamp = (timestamp: number) => {
  return Date.now() < timestamp
}
