import { useRef, useEffect } from 'react'
import React from 'react'
import CountUp from 'react-countup'

const usePrevious = <T extends unknown>(value: T, init: T) => {
  const ref = useRef<T>(init)
  useEffect(() => {
    ref.current = value
  }, [value])
  return ref.current
}

interface Props {
  name: string
  value: number
  countUp?: boolean
  decimal?: number
}

const Currency: React.FC<Props> = ({ name, value, decimal = 4, countUp = false }) => {
  const prevValue = usePrevious<number>(value, 0);
  if (!countUp) return (
    <span>{value.toFixed(decimal)} {name}</span>
  )
  return (
    <CountUp start={prevValue} end={value} suffix={name} duration={0.8} decimals={decimal} />
  )
}

export default Currency
