import { useCallback, useMemo } from 'react'
import { toast } from 'react-toastify'
import { usePool } from 'hooks/pool'
import { MAX_APPROVE_AMOUNT } from 'utils/number'
import { isFutureTimestamp } from 'utils/time'
import { calRewards } from './utils'

export const usePoolLogic = (tokenAddress: string, saverAddress: string) => {
  const { withdraw, deposit, approve, lockProgram, userLocked, totalLocked, allowance } = usePool(tokenAddress, saverAddress)
  console.log(tokenAddress, allowance)
  const present = usePoolView({ allowance, userLocked, endTime: lockProgram.endTime })

  const approveMax = useCallback(() => {
    return approve(MAX_APPROVE_AMOUNT)
  }, [approve])

  const handleApprove = useCallback(() => {
    toast.promise(
      approveMax(),
      {
        pending: '',
        success: '',
        error: ''
      }
    )
  },[approveMax])

  const handleDeposit = useCallback((amount: string) => {
    toast.promise(
      deposit(amount),
      {
        pending: '',
        success: '',
        error: ''
      }
    )
  },[deposit])

  const handleWithdraw = useCallback((amount: string) => {
    toast.promise(
      withdraw(amount),
      {
        pending: '',
        success: '',
        error: ''
      }
    )
  },[withdraw])

  const estimateRewards = useCallback((depositAmount: number | string) => {
    return calRewards(lockProgram, depositAmount)
  }, [lockProgram])

  return {
    data: {
      userLocked,
      totalLocked,
      lockProgram,
      handleWithdraw,
      handleDeposit,
      handleApprove,
      estimateRewards,
    },
    present,
  }
}

type PoolData = {
  allowance: number
  userLocked: number
  endTime: number
}
const usePoolView = ({allowance, userLocked, endTime}: PoolData) => {
  const isFinished = useMemo(() => {
    return !isFutureTimestamp(endTime)
  }, [endTime])

  const showApprove = useMemo(() => {
    return allowance <= 0 && !isFinished
  }, [allowance, isFinished])

  const showDeposit = useMemo(() => {
    return allowance > 0 && !isFinished
  }, [allowance, isFinished])

  const showWithdraw = useMemo(() => {
    return userLocked > 0 && isFinished
  }, [userLocked, isFinished])

  return {
    showApprove,
    showDeposit: !showApprove,
    showWithdraw: !showApprove,
  }
}
