import React, { useCallback, useEffect, useState } from 'react'
import { ModalProps, Modal } from 'components/Modal'
import { Card, CardHeader, CardBody } from 'components/Card'
import { Text } from 'components/Text'
import { Input } from 'components/Input'
import { Flex } from 'components/Box'
import { Button } from 'components/Button'
import Currency from 'components/Currency'
import CloseIcon from 'components/Svg/Icons/Close'

interface DepositCardProps {
  handleDeposit: (amount: string) => void
  estimateRewards: (depositAmount: string | number) => number
  onClose: () => void
  tokenSymbol: string
  tokenBalance: number
}
const DepositCard: React.FC<DepositCardProps> = ({ handleDeposit, estimateRewards, onClose, tokenSymbol, tokenBalance}) => {
  const [amount, setAmount] = useState('')
  const [rewards, setRewards] = useState(0)
  
  useEffect(() => {
    const _rewards = estimateRewards(amount)
    setRewards(_rewards)
  }, [amount, setRewards, estimateRewards])


  const handleSubmit = useCallback(() => {
    handleDeposit(amount)
  }, [handleDeposit, amount])

  return (
    <Card>
      <CardHeader variant='bubblegum'>
        <Flex justifyContent='space-between'>
          <Text>{tokenSymbol}</Text>
          <CloseIcon onClick={onClose} color='primary' />
        </Flex>
      </CardHeader>
      <CardBody>
        <Flex justifyContent='space-between' mb={1}>
          <Text>{tokenSymbol}</Text>
        </Flex>
        <Input type='number' value={amount} onChange={(e) => setAmount(e.target.value)} />
        <Flex justifyContent='space-between' mt={4}>
          <Text fontSize={1}></Text>
          <Text bold fontSize={1}>
            <Currency name={'LOCK'} value={rewards} decimal={4} />
          </Text>
        </Flex>
        <Button onClick={handleSubmit} fullWidth mt={4}></Button>
      </CardBody>
    </Card>
  )
}

interface Props extends ModalProps, DepositCardProps {}

const DepositModal: React.FC<Props> = ({ handleDeposit, estimateRewards, tokenSymbol, tokenBalance, ...props }) => {
  return (
    <Modal {...props}>
      <DepositCard
        handleDeposit={handleDeposit}
        estimateRewards={estimateRewards}
        onClose={props.onClose}
        tokenSymbol={tokenSymbol}
        tokenBalance={tokenBalance}
      />
    </Modal>
  )
}

export default DepositModal
