import React from 'react'
import Styled from 'styled-components'
import { layout, space, LayoutProps, SpaceProps } from 'styled-system'
import { Text } from 'components/Text'

interface StyledSectionProps extends LayoutProps, SpaceProps {}
const StyledSection = Styled.section<StyledSectionProps>`
  ${layout}
  ${space}
`

interface DividerProps extends SpaceProps {
  height: number
}
const Divider = Styled.div<DividerProps>`
  background: ${({theme}) => theme.colors.textDisabled};
  height: ${({height}) => `${height}px`};
  width: 100%;
  ${space}
`

interface Props {
  title: string
}
const Section: React.FC<Props> = ({ title, children }) => {
  return (
    <StyledSection p={2} my={2}>
      <Text fontSize={[3,4,4]}>{title}</Text>
      <Divider height={1} mt={2} mb={3} />
      {children}
    </StyledSection>
  )
}

export default Section
