import React, { useMemo } from 'react'
import { getReadableFromNow } from 'utils/time'

interface Props {
  timestamp: number
}
const ReadableFromNow: React.FC<Props> = ({ timestamp }) => {
  const readable = useMemo(() => {
    return getReadableFromNow(timestamp)
  }, [timestamp])
  return (
    <span>{readable}</span>
  )
}

export default ReadableFromNow
