import React, { useState } from 'react'
import { Text } from 'components/Text'
import { CardHeader, CardBody, Card } from 'components/Card'
import { Button } from 'components/Button'
import Currency from 'components/Currency'
import DepositModal from './DepositModal'
import WithdrawModal from './WithdrawModal'
import { usePoolLogic } from '../usePoolLogic'
import { Box, Flex } from 'components/Box'
import ReadableFromNow from './ReadableTimeFromNow'
import ReadableTimestamp from './ReadableTimeStamp'

interface Props {
  saverAddr: string
  tokenSymbol: string
  tokenAddr: string
}

const EventCard: React.FC<Props> = ({ saverAddr, tokenAddr, tokenSymbol }) => {
  const { data, present } = usePoolLogic(tokenAddr, saverAddr)
  const [ isDeposit, setIsDeposit ] = useState(false)
  const [ isWithdraw, setIsWithdraw ] = useState(false)

  return (
    <Card>
      <DepositModal
        isOpen={isDeposit}
        onClose={() => setIsDeposit(false)}
        handleDeposit={data.handleDeposit}
        estimateRewards={data.estimateRewards}
        tokenBalance={data.userLocked}
        tokenSymbol={tokenSymbol}
      />
      <WithdrawModal
        isOpen={isWithdraw}
        onClose={() => setIsWithdraw(false)}
        handleWithdraw={data.handleWithdraw}
        tokenBalance={data.userLocked}
        tokenSymbol={tokenSymbol}
      />
      <CardHeader title={`${tokenSymbol} `} p='16px'>
        <Text bold>
          {`${tokenSymbol} `}
        </Text>
      </CardHeader>
      <CardBody>
        <Flex justifyContent='space-between' m={2}>
          <Text></Text>
          <Text>
            <ReadableFromNow timestamp={data.lockProgram.endTime} />
          </Text>
        </Flex>
        <Flex justifyContent='space-between' m={2}>
          <Text></Text>
          <Text>
            <ReadableTimestamp timestamp={data.lockProgram.endTime} />
          </Text>
        </Flex>
        <Flex justifyContent='space-between' m={2}>
          <Text></Text>
          <Currency name={tokenSymbol} value={data.totalLocked} />
        </Flex>
        <Flex justifyContent='space-between' m={2}>
          <Text></Text>
          <Currency name={tokenSymbol} value={data.userLocked} />
        </Flex>
          <Box p={1} mt={4}>
            {present.showApprove &&
              <Button onClick={data.handleApprove} fullWidth></Button>
            }
            <Flex justifyContent='space-between'>
            {present.showDeposit &&
              <Button onClick={() => setIsDeposit(true)} mr={2}></Button>
            }
            {present.showWithdraw &&
              <Button onClick={() => setIsWithdraw(true)}></Button>
            }
            </Flex>
          </Box>
      </CardBody>
    </Card>
  )
}

export default EventCard;
