import { LockProgram } from 'hooks/pool'

export const calRewards = (program: LockProgram, _depositAmount: string | number) => {
  const startTime = program.startTime
  const endTime = program.endTime
  const period = endTime - startTime
  const depositTp = Date.now()

  console.log(startTime, endTime, depositTp)

  if (depositTp < startTime || depositTp > endTime || !_depositAmount) return 0

  const depositAmount = Number(_depositAmount)
  const effectivePeriod = endTime - depositTp
  const ratio = program.rewardRatio

  const timeFactor = (effectivePeriod / period) ** 2
  const res = depositAmount * timeFactor * ratio
  return res
}
